      reQueryValveCurrentData (){
        this.queryParam.currentPage = 1;
        this.queryValveCurrentData ();
      },
      queryValveCurrentData (){
        console.info("组织机构ID："+this.treeNodeInfo.treeNodeOrgId);
        let that = this;
        let pui_company_id ='';
        let pui_child_company_id ='';
        let pui_institute_id ='';
        let pui_station_id ='';
        let pui_community_id ='';
        let pui_build_id ='';
        if(this.pri_detail.treeNodeType !== undefined ){
          let count = this.pri_detail.treeNodeType.length ;
          let type = this.pri_detail.treeNodeType[count-1];

          if(type === "1"){
            pui_company_id = this.pri_detail.treeNodeIds[count-1] ;
          }else if(type === "2"){
            pui_child_company_id = this.pri_detail.treeNodeIds[count-1] ;
          }else if(type === "3"){
            pui_institute_id = this.pri_detail.treeNodeIds[count-1] ;
          }else if(type === "4"){
            pui_station_id = this.pri_detail.treeNodeIds[count-1] ;
          }
        }
        if(JSON.stringify(this.dataQuery) !== '{}'  && this.dataQuery.community !== undefined){
          pui_community_id =  this.dataQuery.community.ci_uuid
        }
        if(JSON.stringify(this.dataQuery) !== '{}'  && this.dataQuery.builder !== undefined){
          pui_build_id =  this.dataQuery.builder.bi_uuid
        }
        let _jsondata_ =   {
          perPageSize:this.queryParam.pageSize,
          currentPage:this.queryParam.currentPage,
          pui_realname:this.ruleForm.pui_realname,
          pui_company_id:pui_company_id,
          pui_child_company_id:pui_child_company_id,
          pui_institute_id:pui_institute_id,
          pui_station_id:pui_station_id,
          pui_community_id:pui_community_id,
          pui_build_id:pui_build_id,
          pui_power_status:this.ruleForm.pui_power_status,
          pui_room_position:this.ruleForm.pui_room_position,
          pui_room_toward:this.ruleForm.pui_room_toward,
          pui_room_type:this.ruleForm.pui_room_type,
          pui_power_type:this.ruleForm.pui_power_type,
          stationIds : this.stationIds,

        } ;
        if(this.sortOrderCol!==''){
          _jsondata_.orderby = this.sortOrderCol;
        }
        //console.info("json:"+JSON.stringify(_jsondata_));
        // const loading = this.$loading({
        //   lock: true,
        //   text: '查询中...',
        //   target: '.main',
        //   spinner: 'el-icon-loading',
        //   background: 'rgba(255, 255, 255, 0)'
        // });
        var p1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.residentialHousehold_query,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          {"Content-Type": "application/x-www-form-urlencoded"},
          this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
        );

        p1.then((res) => {
          console.log("异步调用成功返回:"+JSON.stringify(res));
          if(res.data.status=='success'){

            // loading.close();
            that.tableData = res.data.rows;
            that.queryParam.totalRow = res.data.total;

          }else{
            console.info("查询失败");
            // loading.close();

            that.tableData = [];
