      handleDeleteRow(index, row) {
        console.log("行号："+index+",行内容：" + JSON.stringify(row) );
        this.$confirm('请再次确认是否要删除[<span style="color:red;">' + row.pui_realname + '</span>]该行记录?', '删除提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          dangerouslyUseHTMLString: true,
          closeOnClickModal: false,
          closeOnPressEscape: false,

        }).then(() => {
          let _jsondata_ =   {
            pui_uuid:row.pui_uuid,
          } ;
          var p1 = this.customGlobal.postHttpClient(
            this.customGlobal.serverUrl.residentialHousehold_delete,
            //this.customGlobal.serverUrl.baseURI,
            this.customGlobal.serverUrl.orgBaseURI,
            {"Content-Type": "application/x-www-form-urlencoded"},
            this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
          );
          p1.then((res) => {
            console.log("异步调用成功返回:"+JSON.stringify(res));
            if(res.data.status=='success'){
              this.$message({
                type: 'success',
                showClose: true,
                duration:3000,
                dangerouslyUseHTMLString: true,
                message: '<span style="color:black;">基础信息 ['+row.pui_realname+'] 删除成功 </span>'
              });
              //删除成功后清空之前选中数据
              this.$refs.multipleTable.clearSelection();
              this.reQueryValveCurrentData() ;
            }else{
              console.info("删除失败");
              this.$alert(res.data.msg, '删除失败', {
                confirmButtonText: '确定',
                callback: action => {
                  console.info('data:'+JSON.stringify(res.data));
                  if(res.data.msg==='请登录认证'){
                    this.$router.push({path:'/login'});
                  }
                }
              });
            }
          },error=>{
            console.log("异步调用出错:"+JSON.stringify(error));
            this.$alert(JSON.stringify(error.message), '删除出错!', {
              confirmButtonText: '确定',
              callback: action => {
              }
            });
          }).catch((res) => {
            console.log("异步调用失败返回:"+JSON.stringify(res));
            this.$alert(JSON.stringify(res.message), '删除出错', {
              confirmButtonText: '确定',
              callback: action => {
              }
            });
          });
        }).catch((res) => {
          console.log("取消删除:"+JSON.stringify(res));
          this.$message({
            type: 'info',
            showClose: true,
            duration:3000,
            dangerouslyUseHTMLString: true,
            message: '已取消删除'
          });
        });
      },
      deleteUserArray(){
        if(this.multipleSelection.length==0){
          this.$alert('您还未选择要删除的用户！', '删除提示', {
            confirmButtonText: '确定',
            callback: action => {
            }
          });
          return;
        }
        let puiUuids="";
        for (let i = 0; i <this.multipleSelection.length ; i++) {
          puiUuids += "'"+this.multipleSelection[i].pui_uuid+"',";
        }
        let count = this.multipleSelection.length;
        this.$confirm('请再次确认是否要删除已选中的'+count+'位用户?', '删除提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          dangerouslyUseHTMLString:true,
          closeOnClickModal:false,
          closeOnPressEscape:false,
        }).then(() => {

          let _jsondata_ =   {
            puiUuids:puiUuids.substring(0,puiUuids.length-1)
          } ;
          const loading = this.$loading({
            lock: true,
            text: '删除中...',
            target: '.main',
            spinner: 'el-icon-loading',
            background: 'rgba(255,255, 255, .0)'
          });
          //此方法需要4个参数：方法地址、服务器地址、post头参数、传递的参数
          var p1 = this.customGlobal.postHttpClient(
            // this.customGlobal.serverUrl.residentialHousehold_delete_batch,
            'fr/residentialHousehold_delete_batch',
            //this.customGlobal.serverUrl.baseURI,
            this.customGlobal.serverUrl.orgBaseURI,
            {"Content-Type": "application/x-www-form-urlencoded"},
            this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
          );
          //post提交后返回值操作
          p1.then((res) => {
            if(res.data.status=='success'){
              loading.close();
              //删除成功后清空之前选中数据
              this.$refs.multipleTable.clearSelection();
              this.$message({
                type: 'success',
                showClose: true,
                duration:3000,
                dangerouslyUseHTMLString: true,
                message: '<span style="color:black;">基础信息 ['+count+'] 位用户批量删除成功 </span>'
              });

              this.reQueryValveCurrentData() ;
            }else{
              console.info("批量删除失败");
              loading.close();
              this.$alert(res.data.msg, '批量删除失败', {
                confirmButtonText: '确定',
                callback: action => {
                  console.info('data:'+JSON.stringify(res.data));
                  if(res.data.msg==='请登录认证' && res.data.status==='failed'){
                    this.$router.replace({path:'/login'});
                  }
                }
              });
            }
          },error=>{
            console.log("异步调用出错:"+JSON.stringify(error));
            loading.close();
            this.$alert(JSON.stringify(error.message), '批量删除出错!', {
              confirmButtonText: '确定',
              callback: action => {

              }
            });
          }).catch((res) => {
