      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let that = this;

            let _jsondata_ =   {
              pui_uuid:that.userForm.pui_uuid,
              pui_company_id :that.userForm.pui_company_id ,
              pui_child_company_id :that.userForm.pui_child_company_id ,
              pui_institute_id :that.userForm.pui_institute_id ,
              pui_station_id :that.userForm.pui_station_id ,
              pui_community_id :that.userForm.pui_community_id ,
              pui_build_id :that.userForm.pui_build_id ,
              pui_realname:that.userForm.pui_realname,
              pui_concat_phone:that.userForm.pui_concat_phone,
              pui_power_status:that.userForm.pui_power_status,
              pui_build_layer:that.userForm.pui_build_layer,
              pui_build_unit:that.userForm.pui_build_unit,
              pui_build_room_code:that.userForm.pui_build_room_code,
              pui_power_area:that.userForm.pui_power_area,
              pui_room_position:that.userForm.pui_room_position,
              pui_room_toward:that.userForm.pui_room_toward,
              pui_room_type:that.userForm.pui_room_type,
              pui_power_type:that.userForm.pui_power_type,
              pui_position:that.userForm.pui_position,
              flag : that.userForm.flag
            } ;
            const loading = this.$loading({
              lock: true,
              text: '提交中...',
              target: '.main',
              spinner: 'el-icon-loading',

              background: 'rgba(255,255, 255, .0)'
            });

            if(that.userForm.flag === '1'){
              var p1 = this.customGlobal.postHttpClient(
                this.customGlobal.serverUrl.residentialHousehold_add,
                //this.customGlobal.serverUrl.baseURI,
                this.customGlobal.serverUrl.orgBaseURI,
                {"Content-Type": "application/x-www-form-urlencoded"},
                this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
              );
              p1.then((res) => {
                console.log("异步调用成功返回:"+JSON.stringify(res));
                if(res.data.status=='success'){
                  this.userDialogFormVisible =false;//提交成功关闭dialog
                  loading.close();
                  this.$message({
                    type: 'success',
                    showClose: true,
                    duration:3000,
                    dangerouslyUseHTMLString: true,
                    message: '<span style="color:black;">基础信息 ['+that.userForm.pui_realname+'] 新增成功 </span>'
                  });
                  this.queryValveCurrentData();
                }else{
                  console.info("新增失败");
                  loading.close();
                  this.$alert(res.data.msg, '新增失败', {
                    confirmButtonText: '确定',
                    callback: action => {
                      console.info('data:'+JSON.stringify(res.data));
                      if(res.data.msg==='请登录认证'){
                        this.$router.push({path:'/login'});
                      }
                    }
                  });
                }

              },error=>{
                console.log("异步调用出错:"+JSON.stringify(error));
                loading.close();
                this.$alert(JSON.stringify(error.message), '新增出错!', {
                  confirmButtonText: '确定',
                  callback: action => {

                  }
                });
              }).catch((res) => {
                console.log("异步调用失败返回:"+JSON.stringify(res));
                loading.close();
                this.$alert(JSON.stringify(res.message), '新增出错', {
                  confirmButtonText: '确定',
                  callback: action => {

                  }
                });
              });
              this.queryValveCurrentData();
            }else{
              var p1 = this.customGlobal.postHttpClient(
                this.customGlobal.serverUrl.residentialHousehold_update,
                //this.customGlobal.serverUrl.baseURI,
                this.customGlobal.serverUrl.orgBaseURI,
                {"Content-Type": "application/x-www-form-urlencoded"},
                this.qs.stringify({jsondata:JSON.stringify(_jsondata_)})
              );
              p1.then((res) => {
                console.log("异步调用成功返回:"+JSON.stringify(res));
                if(res.data.status=='success'){
                  this.userDialogFormVisible =false;//提交成功关闭dialog
                  loading.close();
                  this.$message({
                    type: 'success',
                    showClose: true,
                    duration:3000,
                    dangerouslyUseHTMLString: true,
                    message: '<span style="color:black;">基础信息 ['+that.userForm.pui_realname+'] 修改成功 </span>'
                  });
                  this.queryValveCurrentData();
                }else{
                  console.info("修改失败");
                  loading.close();
                  this.$alert(res.data.msg, '修改失败', {
                    confirmButtonText: '确定',
                    callback: action => {
                      console.info('data:'+JSON.stringify(res.data));
                      if(res.data.msg==='请登录认证'){
                        this.$router.push({path:'/login'});
                      }
                    }
                  });
                }

              },error=>{
                console.log("异步调用出错:"+JSON.stringify(error));
                loading.close();
                this.$alert(JSON.stringify(error.message), '修改出错!', {
                  confirmButtonText: '确定',
                  callback: action => {

                  }
                });
              }).catch((res) => {
                console.log("异步调用失败返回:"+JSON.stringify(res));
                loading.close();
                // this.userDialogFormVisible =false;//提交失败关闭dialog
                this.$alert(JSON.stringify(res.message), '修改出错', {
                  confirmButtonText: '确定',
                  callback: action => {

                  }
                });
              });
              this.queryValveCurrentData();
            }

          } else {
            console.log('error submit!!');
            return false;
          }
        });

      },
      removeTreeNode (node, data) {
        const parent = node.parent
        const children = parent.data.detail || parent.data
        const index = children.findIndex(d => d.id === data.id)
        children.splice(index, 1)
      },
      initTreeDataStatus(h, { node, data, store }) {

        if(JSON.stringify(this.treeDataStatus)==="{}"){
          return(
            <span style="font-size:14px;">
            <span>{data.caption }</span>
            </span>
        )
        }else{
          let find = false;
          for (let k of Object.keys(this.treeDataStatus)) {
            if(data.id===k){
              //console.info(k+",显示"+data.caption );
              return(
                <span >
                <span>{data.caption }</span>
                </span>
            )
              find = true;
              break;
            }else{
            }
          }
          if(find === false){
            this.removeTreeNode( node,data);
          }
        }
      },
      initOrgTemplateInfo(){
        const loading = this.$loading({
          lock: true,
          text: '查询中...',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(255, 255, 255, 0)'
        });
        let p1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.postQueryOrganTemplate,
          //this.customGlobal.serverUrl.baseURI,
          this.customGlobal.serverUrl.orgBaseURI,
          {"Content-Type": "application/x-www-form-urlencoded"},
          this.qs.stringify({jsondata:JSON.stringify({config:'all',username:this.customGlobal.userInfoExt.cui_login_id})})
        );

        p1.then((res) => {
          console.log("异步调用成功返回:"+JSON.stringify(res));
          if(res.data.status=='success'){
            if( this.customGlobal.userInfoExt.cui_type !== '0' ){
              this.treeData = JSON.parse(res.data.organ).detail;
            }else{
              this.treeData =  [JSON.parse(res.data.organ)];
            }
            this.treeDataStatus = JSON.parse(res.data.orgkey);
            loading.close();
          }else{
            console.info("查询失败");
            this.treeData = [ JSON.parse(res.data.organ).detail ];
            this.treeDataStatus = JSON.parse(res.data.orgkey);
            loading.close();
          }
        },error=>{
          console.log("异步调用出错:"+JSON.stringify(error));
          loading.close();
          this.$alert(JSON.stringify(error.message), '查询出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:"+JSON.stringify(res));
          loading.close();

          this.$alert(JSON.stringify(res.message), '查询出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
      }

    }
  }
</script>

